/*!
 * Datetimepicker for Bootstrap 3
 * version : 4.17.47
 * https://github.com/Eonasdan/bootstrap-datetimepicker/
 */
.bootstrap-datetimepicker-widget {
  list-style: none;
  z-index: 5;
  &.dropdown-menu {
    display: block;
    margin: 2px 0;
    padding: 4px;
    width: 250px;
    z-index: 500;
    &:before, &:after {
      content: '';
      display: inline-block;
      position: absolute;
    }
    &.bottom {
      &:before {
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
        border-bottom: 7px solid #ccc;
        border-bottom-color: rgba(0, 0, 0, 0.2);
        top: -7px;
        left: 7px;
      }
      &:after {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-bottom: 6px solid white;
        top: -6px;
        left: 8px;
      }
    }
    &.top {
      &:before {
        border-left: 7px solid transparent;
        border-right: 7px solid transparent;
        border-top: 7px solid #ccc;
        border-top-color: rgba(0, 0, 0, 0.2);
        bottom: -7px;
        left: 6px;
      }
      &:after {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 6px solid white;
        bottom: -6px;
        left: 7px;
      }
    }
    &.pull-right {
      &:before {
        left: auto;
        right: 6px;
      }
      &:after {
        left: auto;
        right: 7px;
      }
    }
  }
  .list-unstyled {
    margin: 0;
  }
  a[data-action] {
    //padding: 6px 0;
    &:active {
      box-shadow: none;
    }
  }
  .timepicker-hour, .timepicker-minute, .timepicker-second {
    width: 54px;
    font-weight: bold;
    font-size: 1.2em;
    margin: 0;
  }
  button[data-action] {
    padding: 6px;
  }
  .btn {
    color: $clr-pri;
    border-radius: 3px;
    padding: 0px;
    font-size: 12px;
    line-height: 20px;
    background: $white;
    border: 1px solid $clr-pri;
    &[data-action="togglePeriod"] {
      color: $white;
      background: $clr-pri;
      &:hover {
        background: darken($clr-pri, 5%);
        color: $white !important;
      }
    }
    &[data-action="incrementHours"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Increment Hours";
    }
    &[data-action="incrementMinutes"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Increment Minutes";
    }
    &[data-action="decrementHours"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Decrement Hours";
    }
    &[data-action="decrementMinutes"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Decrement Minutes";
    }
    &[data-action="showHours"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Show Hours";
    }
    &[data-action="showMinutes"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Show Minutes";
    }
    &[data-action="togglePeriod"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Toggle AM/PM";
    }
    &[data-action="clear"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Clear the picker";
    }
    &[data-action="today"]::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Set the date to today";
    }
  }
  .picker-switch {
    text-align: center;
    &::after {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      padding: 0;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0;
      content: "Toggle Date and Time Screens";
    }
    td {
      padding: 0;
      margin: 0;
      height: auto;
      width: auto;
      line-height: inherit;
      span {
        line-height: 2.5;
        height: 2.5em;
        width: 100%;
      }
    }
  }
  table {
    width: 100%;
    margin: 0;
    td {
      text-align: center;
      border-radius: 4px;
    }
    th {
      text-align: center;
      border-radius: 4px;
      height: 20px;
      line-height: 20px;
      width: 20px;
      font-weight: 600;
      &.picker-switch {
        width: 145px;
      }
      &.disabled {
        background: none;
        color: #777777;
        cursor: not-allowed;
        &:hover {
          background: none;
          color: #777777;
          cursor: not-allowed;
        }
      }
      &.prev::after {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        border: 0;
        content: "Previous Month";
      }
      &.next::after {
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        border: 0;
        content: "Next Month";
      }
    }
    thead tr:first-child th {
      cursor: pointer;
      &:hover {
        background: #eeeeee;
      }
    }
    td {
      height: 54px;
      line-height: 54px;
      width: 54px;
      &.cw {
        font-size: .8em;
        height: 20px;
        line-height: 20px;
        color: #777777;
      }
      &.day {
        height: 20px;
        line-height: 20px;
        width: 20px;
        &:hover {
          background: #eeeeee;
          cursor: pointer;
        }
      }
      &.hour:hover, &.minute:hover, &.second:hover {
        background: #eeeeee;
        cursor: pointer;
      }
      &.old, &.new {
        color: #777777;
      }
      &.today {
        position: relative;
        &:before {
          content: '';
          display: inline-block;
          border: solid transparent;
          border-width: 0 0 7px 7px;
          border-bottom-color: $clr-pri;
          border-top-color: rgba(0, 0, 0, 0.2);
          position: absolute;
          bottom: 4px;
          right: 4px;
        }
      }
      &.active {
        background-color: $clr-pri;
        color: #fff;
        text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
        &:hover {
          background-color: $clr-pri;
          color: #fff;
          text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
        }
        &.today:before {
          border-bottom-color: #fff;
        }
      }
      &.disabled {
        background: none;
        color: #777777;
        cursor: not-allowed;
        &:hover {
          background: none;
          color: #777777;
          cursor: not-allowed;
        }
      }
      span {
        display: inline-block;
        width: 54px;
        height: 54px;
        line-height: 54px;
        margin: 2px 1.5px;
        cursor: pointer;
        border-radius: 4px;
        &:hover {
          background: #eeeeee;
        }
        &.active {
          background-color: $clr-pri;
          color: #fff;
          text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
        }
        &.old {
          color: #777777;
        }
        &.disabled {
          background: none;
          color: #777777;
          cursor: not-allowed;
          &:hover {
            background: none;
            color: #777777;
            cursor: not-allowed;
          }
        }
      }
    }
  }
  &.usetwentyfour td.hour {
    height: 27px;
    line-height: 27px;
  }
  &.wider {
    width: 21em;
  }
  .datepicker-decades .decade {
    line-height: 1.8em !important;
  }
  .material-icons {
    font-size: 24px;
    line-height: 34px;
    vertical-align: middle;
    &.material-icons-time {
      &:before {
        content: 'access_time';
      }
    }
    &.material-icons-calendar {
      &:before {
        content: 'date_range';
      }
    }
    &.material-icons-chevron-up {
      &:before {
        content: 'expand_less';
      }
    }
    &.material-icons-chevron-down {
      &:before {
        content: 'expand_more';
      }
    }
    &.material-icons-chevron-left {
      &:before {
        content: 'chevron_left';
      }
    }
    &.material-icons-chevron-right {
      &:before {
        content: 'chevron_right';
      }
    }
    &.material-icons-screenshot {
      &:before {
        content: 'desktop_windows';
      }
    }
    &.material-icons-trash {
      &:before {
        content: 'delete';
      }
    }
    &.material-icons-remove {
      &:before {
        content: 'close';
      }
    }
  }
  .timepicker {
    padding: 5px 0;
    table {
      td {
        height: auto;
        line-height: normal;
        width: auto;
        span {
          width: 34px;
          height: 34px;
          line-height: 34px;
          margin: 0;
          &.material-icons {
            height: auto;
            width: auto;
            line-height: normal;
          }
        }
      }
    }
    .timepicker-hours, .timepicker-minutes, .timepicker-seconds {
      table {
        td {
          height: 34px;
          line-height: 34px;
          width: 34px;
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .bootstrap-datetimepicker-widget.dropdown-menu.timepicker-sbs {
    width: 38em;
  }
}

@media (min-width: 992px) {
  .bootstrap-datetimepicker-widget.dropdown-menu.timepicker-sbs {
    width: 38em;
  }
}

@media (min-width: 1200px) {
  .bootstrap-datetimepicker-widget.dropdown-menu.timepicker-sbs {
    width: 38em;
  }
}

.input-group.date .input-group-addon {
  cursor: pointer;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.time-field {
  .bootstrap-datetimepicker-widget {
    &.dropdown-menu {
      width: 100%;
    }
  }
}